import { Col, Row, Typography, Table, message, Input,List,Button,Popconfirm,DatePicker } from 'antd'
import React, { useState, useContext, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import { subQuarantineCol } from './tableColumn/subQuarantineColumn'
import { PlusCircleOutlined, EditOutlined, SearchOutlined,CopyOutlined,DeleteOutlined} from '@ant-design/icons';
import EditQuarantine from './modal/editQuarantine'
import AddSubQuarantine from './modal/addSubQuarantine'
import EditSubQuarantine from './modal/editSubQuarantine'
import { useMutation, useQuery } from '@apollo/client'
import { GET_ALL_PERSONINFO_NO_LIMIT} from '../../graphql/people'
import { GET_PERSON_BY_QUARANTINE } from '../../graphql/quarantine'
import { GET_QUARANTINE_BY_ID } from '../../graphql/quarantine'
import { DELETE_PERSON_BY_QUARANTINE } from '../../graphql/quarantine'

const {Title} = Typography
const { RangePicker } = DatePicker

export default function SubQuarantine() {

    const districtLatLong = [
        {
            district:"",
            lat:13.60472222544493,
            long:103.45245485053685,
        },
        {
            district:"",
            lat:13.867238663711532,
            long:103.87715707241854,
        },
        {
            district:"",
            lat:13.852238272312373,
            long: 103.53639730433542,
        },
        {
            district:"",
            lat:13.713723851088394,
            long:103.67219336284299,
        },
        {
            district:"",
            lat:13.605041870228655,
            long:103.97859822744759,
        },
        {
            district:"",
            lat:13.414442053292065,
            long:104.10460200711728,
        },
        {
            district:"",
            lat:13.303005227138778,
            long:103.9796463766434,
        },
        {
            district:"",
            lat:13.440442720250852,
            long:103.71973510206098,
        },
        {
            district:"",
            lat:13.232399982258997,
            long:104.33684532302983,
        },
        {
            district:"",
            lat:13.698615549661712,
            long:104.21744370698126,
        },
        {
            district:"",
            lat:13.483410469370508,
            long:103.87412317588966,
        },
        {
            district:"",
            lat:13.332155324410378,
            long:103.81891026025993,
        },
        
    ]

    let { id } = useParams();

    const [peopleData, setPeopleData] = useState([])
    const [updateSubData, setUpdateSubData] = useState({})

    const [openEdit, setOpenEdit] = useState(false)
    const [openAddSub, setOpenAddSub] = useState(false)
    const [openEditSub, setOpenEditSub] = useState(false)
    const [subQuarantineData, setSubQuarantineData] = useState([])
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(10)
    const [keyword, setKeyword] = useState("")
    const [quarantineData,setQuarantineData] = useState({})

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const {data, refetch:refetchPerson} = useQuery(GET_ALL_PERSONINFO_NO_LIMIT,{
        onCompleted:({allPersonalInfos})=>{
            // console.log(allPersonalInfos)
            setPeopleData(allPersonalInfos)
        }
    })

    const {data:quarantine,loading, refetch:refetchQuarantine} = useQuery(GET_QUARANTINE_BY_ID,{
        variables:{
            id:id
        },
        onCompleted:({getQuarantineInfoById})=>{
            // console.log(getQuarantineInfoById)
            setQuarantineData(getQuarantineInfoById)
        }
    })

    useEffect(()=>{
        if(quarantine){
            setQuarantineData(quarantine?.getQuarantineInfoById)
        }
    },[quarantine])

    const {data:subQuarantine,refetch:refetchSub} = useQuery(GET_PERSON_BY_QUARANTINE,{
        variables:{
            page:page,
            limit:limit,
            keyword:keyword,
            quarantineInfoId:id
        },
        onCompleted:({getPeopleForQuarantineWithPagination})=>{
            // console.log(getPeopleForQuarantineWithPagination)
            setSubQuarantineData(getPeopleForQuarantineWithPagination)
        },
        onError:({error})=>{
            console.log(error)
        }
    })

    useEffect(()=>{
        if(subQuarantine){
            setSubQuarantineData(subQuarantine?.getPeopleForQuarantineWithPagination)
        }
    }, [subQuarantine])

    const [deletePeopleFromQuarantine,{loading:deleteLoading}] = useMutation(DELETE_PERSON_BY_QUARANTINE,{
        onCompleted:({deletePeopleFromQuarantine})=>{
            if(deletePeopleFromQuarantine.success===true){
                refetchSub()
                message.success("")
            }else{
                message.error(deletePeopleFromQuarantine.message)
            }   
            
        }
    })

    const handleDelete = (e) => {
        //subQuarantineDataDispatch({type: "DELETE_SUB_QUARANTINE", payload: e})
        deletePeopleFromQuarantine({
            variables:{
                personalInfoId: e,
                quarantingId:id
            }
        })
    }

    const handleEditSubQuarantine = (e) => {
        
        setUpdateSubData(e)
        setOpenEditSub(true)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }
    }

    const dateHolder = ["", ""]

    const key = 'updatable';
    const openMessage = () => {
        message.loading({ content: 'Loading...', key });
        setTimeout(() => {
          message.success({ content: 'Loaded!', key, duration: 2 });
        }, 1000);
      };

    return (
        <>
        {/* { loading ? message.loading("test"): */}
        <Row>
            <EditQuarantine open={openEdit} setOpen={setOpenEdit} data={quarantineData} quarantineId={id} setRefetch={refetchQuarantine}/>
            <AddSubQuarantine open={openAddSub} setOpen={setOpenAddSub} quarantineId={id} peopleData={peopleData} setRefetch={refetchSub} />
            <EditSubQuarantine open={openEditSub} setOpen={setOpenEditSub} data={updateSubData} quarantineId={id} peopleData={peopleData} setRefetch={refetchSub} />
            <Col
                xs={24}
            >
                {/* <Title level={5}> 
                    <EditOutlined className="link" onClick={() => setOpenEdit(true)}/>
                </Title> */}
            </Col>
            <Col
                xs={24}
                md={11}
                className="subCase-card"
            >
                <p> {quarantineData?.locationName} <EditOutlined className="link" onClick={() => setOpenEdit(true)}/></p>
                {/* <p> {quarantineData?.place}</p> */}
                <p> {quarantineData?.locationType}</p>
                <p> {quarantineData?.forPeopleType}</p>
                <p> 
                    {quarantineData?.village !== "" && quarantineData?.village + ","}
                    {quarantineData?.commune !== "" && quarantineData?.commune + ","}
                    {quarantineData?.district !== "" && quarantineData?.district + ","}
                    {quarantineData?.province}
                </p>
                <p> {quarantineData?.personInCharge?.lastName} {quarantineData?.personInCharge?.firstName}</p>
                <p> {quarantineData?.personInCharge?.tel}</p>
                <p> {quarantineData?.other}</p>
            </Col>
            {/* <Col
                xs={24}
                md={{ span: 11, offset: 2 }}
                className="subCase-card"
            >
                <p> </p>
                <EditOutlined className="link" onClick={() => setOpenEdit(true)}/>
                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
            </Col> */}

            <Col
                xs={24}
                md={24}
                style={{ margin: "30px 0 0 0" }}
            >
                <Row>
                    <Col
                        xs={24} 
                        md={10}
                        style={{ marginTop: 30, marginBottom: 15 }}
                    >
                    <Title level={5}>
                        {" "}

                        <PlusCircleOutlined className="link" onClick={() => setOpenAddSub(true)}/>
                    </Title>
                    </Col>
            
                    <Col
                        xs={24} 
                        md={7}
                        style={{ marginTop: 30, marginBottom: 15 }}
                    >
                        <RangePicker 
                            // onChange={(e) => setFilterDate(e)}  
                            size="large"
                            onChange={e => onSelectDate(e)} 
                            placeholder={dateHolder} 
                            style={{width:"100%"}} 
                        />
                    </Col>
                    <Col
                    xs={{span: 24, offset: 0}}
                    md={{span: 6, offset: 1}}
                    style={{ marginTop: 30, marginBottom: 15 }}
                    >
                    <Input prefix={<SearchOutlined />} placeholder="..." size="large" onChange={e => setKeyword(e.target.value) }/>
                    </Col>
                </Row>
            </Col>
            <Col
                xs={24}
                md={24}
            >
                <Table
                    columns={subQuarantineCol({handleDelete, handleEditSubQuarantine,limit,page})}
                    dataSource={subQuarantineData?.personalInfos}
                    rowKey={(record) => record.id}
                    expandRowByClick={true}
                    // expandedRowKeys
                    expandable={{
                        
                        expandedRowRender: record => (
                        <List
                            // bordered
                            dataSource={record.quaranting}
                            renderItem={item => (
                                <List.Item>
                                    <Row>
                                        
                                        <Col
                                            xs={24}
                                            md={24}
                                        >
                                            {/* <Typography.Text type={item.result ? "danger" : "success"}>[{item.result ? "" : ""}]  </Typography.Text>
                                            {item.covidVariant}&emsp;<br/> */}
                                            &emsp;{item.date_in === null || item.date_in === undefined ? "" : moment.parseZone(item.date_in).format("DD MM YYYY")}<br/>
                                            &emsp;{item.date_out === null || item.date_out === undefined ? "" : moment.parseZone(item.date_out).format("DD MM YYYY")},{item.date_out}<br/>
                                            &emsp;{item.personTypes === null || item.personTypes === undefined ? "" : item.personTypes}<br/>
                                            &emsp;{item.coorporate ? "":""}<br/>
                                        </Col>

                                            <Col
                                                xs={24}
                                                md={24}
                                            >
                                                <br/>
                                                {/* <Button onClick={()=> {handleLabForm({personData:record,labData:item})}}><CopyOutlined  style={{ color: "green" }}/></Button> */}
                                                <Button onClick={()=> handleEditSubQuarantine({...item,personalInfoId:record.id})}><EditOutlined  style={{ color: "green" }}/></Button>
                                                <Popconfirm
                                                    title="?"
                                                    onConfirm={ ()=>handleDelete({sampleTestId:item.id,personalInfoId:record.id})}
                                                    okText=""
                                                    cancelText=""
                                                >
                                                    <Button><DeleteOutlined style={{ color: "red" }} /></Button>
                                                </Popconfirm>
                                            </Col>
                                    </Row>
                                </List.Item>
                                )}
                        />
                       ),
                      }}
                    pagination={{
                        total: subQuarantineData?.paginator?.totalDocs,
                        // showSizeChanger: true,
                        onChange:((page, pageSize) => {setPage(page);setLimit(pageSize)} )
                    }}
                    scroll={{ x: 240 }}
                    sticky
                />
            </Col>
        </Row>
        {/* } */}
        </>
    )
}
