import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const peopleCol = ({handleDelete,limit,page}) => {
    let no = 0 + (page * limit) - limit
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 60,
            render: (text, record, i) => (
                <span>
                    {no+= 1}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 130,
        },
        {
            title: '',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 140,
        },
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 130,
            render: (text, record) => (
                <span size="middle">
                    {
                        record.lastName+" "+record.firstName
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'nationality',
            key: 'nationality',
            width: 120,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 250,
            render: (text, record) => (
                <span>
                    {(record.village !== "") && record.village+", "}
                    {record.commune !== "" && record.commune+", "}
                    {record.district !== "" && record.district+", "}
                    {record.province && record.province}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'occupation',
            key: 'occupation',
            width: 120,
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
        },
        // {
        //     title: '',
        //     dataIndex: 'other',
        //     key: 'other',
        //     width: 150,
        // },
        {
            title: '',
            dataIndex: 'interview',
            key: 'interview',
            width: 120,
            render: (text, record) => (
                <span>{record?.interviewed ? "" : ""}</span>
                )
        },
        {
            title: '',
            dataIndex: 'currentState',
            key: 'currentState',
            width: 120,
            render: (text, record) => {
                let state ="";
                let classN = "badge-success"
                let c_state = record?.currentState;

                if(c_state.confirm===true){
                    state = ""
                    classN= "badge-error"
                }

                if(c_state.recovered===true){
                    state = ""
                    classN= "badge-success"
                }

                if(c_state.death===true){
                    state = ""
                    classN= "badge-error"
                }

             
                // {
                //     record.closeAt === null ? 
                //     <span className="badge-error"></span> : 
                //     record.openAt === null ? 
                //     <span className="badge-error"></span> : 
                //     <span className="badge-success"></span>
                // }

                return (
                    <span className={classN}>{state}</span>
                )
            }
                
            
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 80,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}