import React from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button } from 'antd'

export default function Tab1({open, setOpen}) {

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="intro"
            footer={null}
        >
            <Form
                name="Intro"
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
