import React, { useContext, useState, useEffect,useRef } from 'react'
import { Row, Col, Button, Input, Table, message, List, Typography,Popconfirm,DatePicker,Modal,Form} from 'antd'
import { PlusOutlined,DeleteOutlined, EditOutlined,CopyOutlined } from '@ant-design/icons';
import ReactToPrint from 'react-to-print';
import { sampleCol } from '../component/sample/tableColumn/sampleCols'
import AddSampleTest from '../component/sample/modal/addSampleTest';
import EditSampleTest from '../component/sample/modal/editSampleTest';
import { useQuery, useMutation } from '@apollo/client';
import { GET_ALL_QUARANTINEINFO } from '../graphql/quarantine';
import { DELETE_QUARANTINE_BY_ID } from '../graphql/quarantine';
import { GET_ALL_PERSONINFO_LIMIT_FIELDS } from '../graphql/people'
import { GET_ALL_SAMPLE } from '../graphql/sample'
import { GET_ALL_LOCATION_SAMPLE } from '../graphql/location'
import { DELETE_SAMPLETEST } from '../graphql/sample';
import ReportLaboratory from '../component/print/reportLaboratory'
import ReportLineList from '../component/print/reportLineList';
import moment from 'moment-timezone';
import ExportToExcel from "../component/export/exportToExcel";
// import Form from 'antd';
import { ListSelect } from "../static/own-comp";
import { GET_ALL_SAMPLE_LOCATION } from '../graphql/sample';
import {useLocation} from 'react-router-dom'

const { RangePicker } = DatePicker;

export default function Sample() {

    // let moment = require('moment-timezone');
    let locationRoute = useLocation()

    const [openAdd, setOpenAdd] = useState(false)
    const [openEdit, setOpenEdit] = useState(false)
    const [editData,setEditData] = useState({})

    let dataToExcel = []
    let personalDataWithNo = []

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    const [startDate, setStartDate] = useState(null)
    const [endDate, setEndDate] = useState(null)
    const [sampleData, setSampleData] = useState([])

    const [locationTest,setLocationTest] = useState(null)

    const componentRef3 = useRef()
    const [openLabForm,setOpenLabForm] = useState(false)
    const [labFormData, setLabFormData] = useState({})

    const componentRef4 = useRef()
    const [openLineList,setOpenReportLineList] = useState(false)
    const [lineListData, setLineListData] = useState({})
    const [locationForFilter, setLocationForFilter] = useState([])

    

    // const { data:location } = useQuery(GET_ALL_LOCATION_SAMPLE)s

    const { data:locationFilter } = useQuery(GET_ALL_SAMPLE_LOCATION,{
        onCompleted:({getSampleTestLocation})=>{
            // console.log(getSampleTestLocation)
            setLocationForFilter(getSampleTestLocation)
        },
        nextFetchPolicy:'cache-only'
    })

    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_LIMIT_FIELDS)
    const getPersonalInfo = personalInfo?.allPersonalInfos
    // const getLocation = location?.allAffectedLocations

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const { data, loading, error, refetch } = useQuery(GET_ALL_SAMPLE, {
        variables: {
            page: page,
            limit: limit,
            keyword: keyword,
            startDate:ranges.startDate,
            endDate:ranges.endDate,
            testLocation: locationTest
        },
        onCompleted: ({ getPeopleForSampleTestWithPagination }) => {
            // console.log(getPeopleForSampleTestWithPagination)
            // message.loading("...")
            setSampleData(getPeopleForSampleTestWithPagination)
            
        },
        fetchPolicy: 'network-only',
        nextFetchPolicy:"cache-first"
    })

    useEffect(()=>{
        if(data){
            setSampleData(data?.getPeopleForSampleTestWithPagination)
        }
    }, [data])

    //  
    
    const [deleteSampleTest, { loading: deleteLoading }] = useMutation(DELETE_SAMPLETEST, {
        onCompleted: () => {
            refetch()
            message.success("")
        }
    })

    useEffect(() => {
        refetch()
    }, [page, limit, keyword, startDate, endDate, locationTest])

    useEffect(() => {
        if(locationRoute?.state !== undefined ){
            setRanges({
                startDate:locationRoute?.state?.date,
                endDate:locationRoute?.state?.date
            })
        }
    }, [locationRoute])


    const handleDelete = (e) => {
        deleteSampleTest({
            variables: {
                sampleTestId: e.sampleTestId,
                personalInfoId: e.personalInfoId
            }
        })
    }

    const handleEdit=(e)=>{
        setEditData(e)
        setOpenEdit(true)
    }

    const setFilterDate = (e)=>{

        // if(e){
            setStartDate(moment.parseZone(e[0]))
            setEndDate(moment.parseZone(e[1]))
        // }
    }

    const handleLabForm = (e)=>{
        setLabFormData(e)
        setOpenLabForm(true)
    }

    const handleLineList = (e)=>{
        setLineListData(e)
        setOpenReportLineList(true)
    }

    const splitDot = (e)=>{
        let newText = e;
        if(newText!=="" || newText!== undefined){
            // newText="test"
            let text = newText?.split(/[!,?,.,,,]/)
            if(text?.length>0){
                newText="";
                for(let i=0;i<text.length;i++){
                    if(i===text.length-1){
                        newText += text[i];
                        break;
                    }
                    newText += text[i]+"-";
                }
            }
        }
        return newText
    }

    //   to csv header
    const headers = [
        { label: ".", key: "no" },
        { label: "", key: "name" },
        { label: "", key: "englishName" },
        { label: "", key: "age" },
        { label: "", key: "gender" },
        { label: "", key: "tel" },
        { label: "*", key: "province" },
        { label: "*", key: "district" },
        { label: "*", key: "commune" },
        { label: "*", key: "village" },
        { label: "", key: "patientId" },
        { label: "", key: "date"},
        { label: "", key: "confirm" },
        { label: "", key: "direct" },
        { label: "", key: "times" },
        { label: "", key: "vaccinated" },
        { label: "", key: "vaccineType" },
        { label: "", key: "nationality" },
    ];
    //to csv data
    console.log((sampleData?.paginator?.currentPage * 10))
  
    sampleData?.personalInfos?.map((record,index) =>{

        let pageAdd = sampleData?.paginator?.currentPage > 1 ? ((sampleData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}

        let toExcelItem = {
            no:index+1,
            name: record.lastName+" "+record.firstName,
            englishName: record.englishName === null || record.englishName === undefined ? "" : record.englishName,
            age: record.age,
            gender:record.gender,
            tel: record.tel === null || record.tel === undefined ? "" : record.tel,
            province: record.province,
            district: record.district === "" ? "": record.district,
            commune: record.commune === "" ? "": record.commune,
            village: record.village === "" ? "": record.village,
            patientId: splitDot(record.patientId),
            direct: record. affectedFrom.direct ? "":"",
            times: "" +record.sampleTest.length ,
            vaccinated:record.vaccination.times > 0 ? "":"",
            vaccineType: record.vaccination.vaccineType === null || record.vaccination.vaccineType === undefined ? "": record.vaccination.vaccineType,
            nationality:record.nationality
        }

        record?.sampleTest?.map(item=>{
        toExcelItem = {
            ...toExcelItem,
            date: moment.parseZone(item.date).format("D/MM/YYYY") , 
            confirm: item.result ? "Positive":"Negative"

        }
        })
        dataToExcel.push(toExcelItem)
        personalDataWithNo.push(setNoToPeopleData)
    })

    const onSelectDate = (e) => {
        console.log(e)
        console.log(moment.tz((new Date()),"Asia/Phnom_Penh"))
        if(e !== null){
            let startDate = moment(e[0]).format("YYYY-MM-DDTHH:MM:SS.00")
            let endDate = moment(e[1]).format("YYYY-MM-DDTHH:MM:SS.00")

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
            // console.log("ssdf", moment.tz(startDate,"Asia/Phnom_Penh"), "edd", endDate)
        }
    }

    const setToLocationFn = (e) => {

        setLocationTest(e);
      
    };

    const dateHolder = ["", ""]
    let no = (page * limit) - limit
    return (
        <Row>
            <AddSampleTest 
                open={openAdd} 
                setOpen={setOpenAdd} 
                personalAllData = {getPersonalInfo}
                locationAllData = {locationForFilter}
                setRefetch={refetch} 
            />
            <EditSampleTest 
                open={openEdit} 
                setOpen={setOpenEdit} 
                data={editData}
                personalAllData = {getPersonalInfo}
                // locationAllData = {getLocation}
                locationAllData = {locationForFilter}
                setRefetch={refetch} 
            />

            {/* print labform */}
            <Modal
                width={900}
                visible={openLabForm}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef3.current}
                    />
                ]}
                onCancel={() => setOpenLabForm(false)}
            >
                <ReportLaboratory ref={componentRef3} labFormData={labFormData} />
            </Modal>
            
            {/* print line list */}
            <Modal
                width={1100}
                visible={openLineList}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef4.current}
                    />
                ]}
                onCancel={() => setOpenReportLineList(false)}
            >
                <ReportLineList ref={componentRef4} labFormData={lineListData} />
            </Modal>
            
            
            <Col
                xs={24}
                md={6}
            >
                <Button
                    type="primary"
                    onClick={() => setOpenAdd(true)}
                >
                    
                    <PlusOutlined />
                </Button>
            </Col>
            <Col
                xs={24}
                md={18}
            >
                <Row>
                    <Col xs={24} md={{span:4, offset:2}}>
                        <ExportToExcel 
                            headers={headers} 
                            data={
                                dataToExcel
                            } 
                            filename={"sampleTest"} 
                            title={"Export to CSV"}
                        />
                    </Col>
                    <Col xs={24} md={2}>
                        <Button
                            type="primary"
                            onClick={() => handleLineList({data:"test"})}
                        >
                            
                        </Button>
                    </Col>
                    
                    <Col xs={24} md={{span:3, offset:1}}>
                       
                                <ListSelect
                                    type={7}
                                    data={locationForFilter}
                                    title=""
                                    setValue={setToLocationFn}
                                />
                           
                    </Col>
                    <Col xs={24} md={{span:5, offset:1}}>
                        <RangePicker 
                            // onChange={(e) => setFilterDate(e)}  
                            onChange={e => onSelectDate(e)} 
                            placeholder={dateHolder} 
                            style={{width:"100%"}} 
                            showTime
                        />
                    </Col>
                    <Col xs={24} md={{span:5, offset:1}}>
                        <Input.Search
                            onChange={(e) => setKeyword(e.target.value)}
                            placeholder="..."
                        />
                    </Col>
                </Row>
                
               
            </Col>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    // caseCol({handleDelete})
                    className="table-go-list"
                    columns={sampleCol({ handleDelete, limit, page})}
                    dataSource={personalDataWithNo}
                    rowKey={record => record.id}
                    expandRowByClick={true}
                    // expandedRowKeys
                    expandable={{
                        
                        expandedRowRender: record => (
                        <List
                            // bordered
                            dataSource={record.sampleTest}
                            renderItem={item => (
                                <List.Item>
                                    <Row>
                                        
                                        <Col
                                            xs={24}
                                            md={12}
                                        >
                                            <Typography.Text type={item.result ? "danger" : "success"}>[{item.result ? "" : ""}]  </Typography.Text>
                                            {item.covidVariant}&emsp;<br/>
                                            &emsp;{item.symptomStart === null || item.symptomStart === undefined ? "" : moment.parseZone(item.symptomStart).format("DD MM YYYY")}<br/>
                                            &emsp;{item.date === null || item.date === undefined ? "" : moment.parseZone(item.date).format("DD MM YYYY")},{item.date}<br/>
                                            &emsp;{item.resultDate === null || item.resultDate === undefined ? "" : moment.parseZone(item.resultDate).format("DD MM YYYY")}<br/>
                                            &emsp;{item.testType}<br/>
                                            &emsp;{item.times}<br/>
                                        </Col>
                                        <Col
                                            xs={24}
                                            md={12}
                                        >
                                            <br/>
                                            &emsp;{item.specimentType}<br/>
                                            &emsp;{item.testLocation}<br/>
                                            &emsp;{item.laboratory}<br/>
                                            &emsp;{item.labFormCompletedBy}<br/>
                                            &emsp;{item.labFormCompletedByTel}<br/><br/>
                                            </Col>
                                            <Col
                                                xs={24}
                                                md={24}
                                            >
                                                <br/>
                                                <Button onClick={()=> {handleLabForm({personData:record,labData:item})}}><CopyOutlined  style={{ color: "green" }}/></Button>
                                                <Button onClick={()=> handleEdit({...item,personalInfoId:record.id})}><EditOutlined  style={{ color: "green" }}/></Button>
                                                <Popconfirm
                                                    title="?"
                                                    onConfirm={ ()=>handleDelete({sampleTestId:item.id,personalInfoId:record.id})}
                                                    okText=""
                                                    cancelText=""
                                                >
                                                    <Button><DeleteOutlined style={{ color: "red" }} /></Button>
                                                </Popconfirm>
                                            </Col>
                                    </Row>
                                </List.Item>
                                )}
                        />
                       ),
                      }}
                    pagination={{
                        total: sampleData?.paginator?.totalDocs,
                        pageSizeOptions:["10", "20"],
                        // showSizeChanger: true,
                        pageSize:limit,
                        // current: sampleData?.paginator?.currentPage,
                        onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize)})
                    }}
                    scroll={{ x: 1500 }}
                    sticky
                    loading={loading}
                />
            </Col>
            
        </Row>
    )
}
