import React, { Component } from 'react'
import { Row, Col } from 'antd'
import logo from '../../asset/srLogo.png'
import moment from 'moment'

export default class ReportSiemReap extends Component {
    render() {
        const data = this.props.data
        const date = this.props.date
        // console.log(data)
        return (
            <div
                style={{
                    padding: 50
                }}
            >
                <Row
                    style={{
                        position:"relative"
                    }}
                >
                    <div
                        style={{
                            position:"absolute",
                        }}
                    >
                        <center>
                        <img src={logo} width={100}/><br/>
                        <span className="HeaderFont"></span>
                        </center>
                    </div>
                    <Col
                        xs={24}
                    >
                        <center>
                            <p>
                                <span className="HeaderFont"><br />
                                      

                                </span><br />

                                <span className="HeaderFont1"
                                    style={{
                                        fontSize: "300pt !important"
                                    }}
                                >3</span>
                            </p>
                        </center>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <center>
                            <p className="HeaderFont">
                                <br />
                                - ....... <br/>
                                - ...... ......<br/>
                                .......................... 
                            </p>
                        </center>
                    </Col>
                    <Col
                        xs={24}
                    >
                            <p>
                                &emsp;   
                                -  -  {data.recovered.length} 
                                - {data.confirm.length}  {data.death.length} {moment(date[1]).format("DD MM YYYY")}   
                            </p>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <span className="HeaderFont">&emsp;.- </span>
                        <table border={1} className="table2">
                            {/* <thead>
                                <tr>
                                    <th>.</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead> */}
                            <tbody>
                            <tr>
                                    <th>.</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                {/* <tr>
                                    <th colSpan={6} style={{textAlign:"left"}}>.   ......</th>
                                </tr>
                                <tr>
                                    <td>1</td>
                                    <td></td>
                                    <td></td>
                                    <td></td>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th colSpan={6} style={{textAlign:"left"}}>.  ........ </th>
                                </tr> */}
                                {data.confirm.map((load, index) => (
                                    <tr>
                                        <td>{index+=1}</td>
                                        <td>{load.lastName + "" + load.firstName}</td>
                                        <td>{load.gender}</td>
                                        <td>{load.age}</td>
                                        <td>{load.province + " " + load.district + " " + load.commune + " " + load.village}</td>
                                        <td>{load.other}</td>
                                    </tr>
                                ))}
                                
                            </tbody>
                        </table>
                    </Col>
                    <Col
                        xs={24}
                        style={{
                            marginTop: 10
                        }}
                    >
                        <p>
                            &emsp;............ .............................
                            ........................................... ............
                            ........................................................................ 
                        </p>
                    </Col>
                    
                    <Col
                       xs={24}
                    >
                        <span className="HeaderFont">&emsp;.</span>
                        <table border={1} className="table2">
                            {/* <thead>
                                <tr>
                                    <th>.</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead> */}
                            <tbody>
                                <tr>
                                    <th>.</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            {data.recovered.length === 0 ? (
                                <tr>
                                    <td colSpan={6}></td>
                                </tr>
                            ):(
                                data.recovered.map((load, index) => (
                                    <tr>
                                        <td>{index+=1}</td>
                                        <td>{load.lastName + "" + load.firstName}</td>
                                        <td>{load.gender}</td>
                                        <td>{load.age}</td>
                                        <td>{load.province + " " + load.district + " " + load.commune + " " + load.village}</td>
                                        <td>{load.other}</td>
                                    </tr>
                                ))
                            )}
                            </tbody>
                        </table>
                    </Col> 
                    <Col
                       xs={24}
                       style={{
                           marginTop: 10
                       }}
                    >
                        <p>
                            &emsp;.......  ...
                            .......................................... ( ) 
                              
                        </p> 
                    </Col> 
                    <Col
                       xs={24}
                    >
                        <span className="HeaderFont">&emsp;.</span>

                    </Col> 
                    <Col
                       xs={24}
                       style={{
                           marginTop: 10
                       }}
                    >
                        <p>
                            {data.death.length !== 0 ? (
                                <span>
                                    &emsp;  {data.death.length} <br/>
                                    &emsp;.......................... .......... ...........................
                                    - .....................  ............................. 
                                     ....... ............................()<br/>
                                </span>
                            ): (
                                <span>&emsp;  {data.death.length}<br/></span>
                            )}
                            
                            &emsp;  - ........... 
                            ........ ...... ........... ..........................................
                            ...( ) <br/>
                            &emsp;   
                               
                              
                                     / 
                               <br/>
                            &emsp;    
                              
                        </p>
                    </Col> 
                    <Col
                        xs={24}
                        style={{
                            position:"relative",
                            height: 100,
                        }}
                    >
                        <div
                            style={{
                                position:"absolute",
                                right: 0,
                                width: 400,
                            }}
                        >
                            <center>
                                <p>
                                    ........ ..........................  ..<br/>
                                     .........................<br/>
                                    <span className="HeaderFont"></span>
                                </p>
                            </center>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}
