import { Col, Row, Typography, Table, message, Input, DatePicker, List, Button, Popconfirm } from 'antd'
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import { subHospitalCol } from './tableColumn/subHospitalColumn'
import { PlusCircleOutlined, EditOutlined, SearchOutlined, CopyOutlined, DeleteOutlined } from '@ant-design/icons';
import EditHospital from './modal/editHospital'
import AddSubHospital from './modal/addSubHospital'
import EditSubHospital from './modal/editSubHospital'
import { useMutation, useQuery } from '@apollo/client'
import { GET_ALL_PERSONINFO_NO_LIMIT } from '../../graphql/people'
import { GET_PATIENT_HOSPITAL_PAGINATION } from '../../graphql/hospital'
import { GET_HOSPITALINFO_BY_ID } from '../../graphql/hospital'
import { DELETE_PATIENT_TO_HOSPITAL} from '../../graphql/hospital'

const { RangePicker } = DatePicker
const { Title } = Typography

export default function SubHospital() {

    let { id } = useParams();

    const [updateSubData, setUpdateSubData] = useState({})
    const [peopleData, setPeopleData] = useState([])
    const [openEdit, setOpenEdit] = useState(false)
    const [openAddSub, setOpenAddSub] = useState(false)
    const [openEditSub, setOpenEditSub] = useState(false)
    const [subHospitalData, setSubHospitalData] = useState([])
    const [hospitalData, setHospitalData] = useState({})

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(10)
    const [keyword, setKeyword] = useState("")
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const { data: hospital, refetch: refetchHospital } = useQuery(GET_HOSPITALINFO_BY_ID, {
        variables: {
            id: id
        },
        onCompleted: ({ getHospitalInfoById }) => {
            // console.log(getHospitalInfoById)
            setHospitalData(getHospitalInfoById)
        }
    })

    useEffect(() => {
        if (hospital) {
            setHospitalData(hospital?.getHospitalInfoById)
        }
    }, [hospital])

    const { data } = useQuery(GET_ALL_PERSONINFO_NO_LIMIT, {
        onCompleted: ({ allPersonalInfos }) => {
            // console.log(allPersonalInfos)
            setPeopleData(allPersonalInfos)
        }
    })

    const { data: hospitalSub, refetch: refetchSub } = useQuery(GET_PATIENT_HOSPITAL_PAGINATION, {
        variables: {
            page: page,
            limit: limit,
            keyword: keyword,
            startDate: ranges.startDate,
            endDate: ranges.endDate,
            hospitalId: id
        },
        onCompleted: ({ getPatientForHospitalWithPagination }) => {
            setSubHospitalData(getPatientForHospitalWithPagination)
            // console.log(getPatientForHospitalWithPagination)
        }
    })

    useEffect(() => {
        if (hospitalSub) {
            setSubHospitalData(hospitalSub?.getPatientForHospitalWithPagination)
        }
        console.log(subHospitalData)
    }, [hospitalSub])

    useEffect(() => {
        refetchSub()
    }, [page, limit, keyword, ranges])

    const [deletePatientFromHospital, { loading }] = useMutation(DELETE_PATIENT_TO_HOSPITAL, {
        onCompleted: ({deletePatientFromHospital}) => {
            // if (loading) {
            //     message.loading("...")
            // } else {
            //     refetchSub()
            //     message.success("")
            // }
            console.log(deletePatientFromHospital)
            if(deletePatientFromHospital.success === true){
                refetchSub()
                message.success(deletePatientFromHospital.message)
            } else {
                message.error(deletePatientFromHospital.message)
            }

        }
    })

    const handleDelete = (h, p) => {
        //subHospitalDataDispatch({type: "DELETE_SUB_HOSPITAL", payload: e})
        deletePatientFromHospital({
            variables: {
                personalInfoId:p,
                hospitalId:h
            }
        })
        console.log(h, p)
    }

    const handleEditSubHospital = (e) => {
        setUpdateSubData(e)
        setOpenEditSub(true)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }
    }

    const dateHolder = ["", ""]

    return (
        <>
            <Row>
                <EditHospital open={openEdit} setOpen={setOpenEdit} data={hospitalData} hospitalId={id} setRefetch={refetchHospital} />
                <AddSubHospital open={openAddSub} setOpen={setOpenAddSub} hospitalData={hospitalData} peopleData={peopleData} setRefetch={refetchSub} />
                <EditSubHospital open={openEditSub} setOpen={setOpenEditSub} data={updateSubData} hospitalId={id} peopleData={peopleData} setRefetch={refetchSub} />
                <Col
                    xs={24}
                >
                    {/* <Title level={5}> 
                    <EditOutlined className="link" onClick={() => setOpenEdit(true)}/>
                </Title> */}
                </Col>
                <Col
                    xs={24}
                    md={11}
                    className="subCase-card"
                >
                    <table>
                        <tbody>
                            <tr >
                                <td style={{ width: '40%' }}><p></p></td>
                                <td style={{ width: '60%' }}><p> {hospitalData?.hospitalName} <EditOutlined className="link" onClick={() => setOpenEdit(true)} /></p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.village !== "" && hospitalData?.village + ","}
                                    {hospitalData?.commune !== "" && hospitalData?.commune + ","}
                                    {hospitalData?.district !== "" && hospitalData?.district + ","}
                                    {hospitalData?.province}</p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.locationType}</p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.other}</p></td>
                            </tr>
                        </tbody>
                    </table>
                </Col>
                <Col
                    xs={24}
                    md={{span:11, offset: 2}}
                    className="subCase-card"
                >
                    <table>
                        <tbody>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.personInCharge?.lastName} {hospitalData?.personInCharge?.firstName}</p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.personInCharge?.tel}</p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.personInCharge?.position}</p></td>
                            </tr>
                            <tr>
                                <td><p></p></td>
                                <td><p> {hospitalData?.personInCharge?.others}</p></td>
                            </tr>
                        </tbody>
                    </table>
                </Col>

                {/* <Col
                xs={24}
                md={{ span: 11, offset: 2 }}
                className="subCase-card"
            >
                <p> </p>

                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
            </Col> */}


                {/* <Col
                    xs={24}
                    style={{ marginTop: 20 }}
                >
                    <Title level={5}>
                         {" "}

                        <PlusCircleOutlined className="link" onClick={() => setOpenAddSub(true)} />
                    </Title>

                </Col> */}

                <Col
                    xs={24}
                    md={10}
                    style={{ marginTop: 30, marginBottom: 15 }}
                >
                    
                    <Title level={5}>
                             {" "}

                            <PlusCircleOutlined className="link" onClick={() => setOpenAddSub(true)} />
                        </Title>

                </Col>
                <Col 
                    xs={24} 
                    md={7}
                    style={{ marginTop: 30, marginBottom: 15 }}
                >
                    <RangePicker 
                        // onChange={(e) => setFilterDate(e)}  
                        size="large"
                        onChange={e => onSelectDate(e)} 
                        placeholder={dateHolder} 
                        style={{width:"100%"}} 
                    />
                </Col>
                <Col
                    xs={{span: 24, offset: 0}}
                    md={{span: 6, offset: 1}}
                    style={{ marginTop: 30, marginBottom: 15 }}
                >
                    <Input prefix={<SearchOutlined />} placeholder="..." size="large" onChange={e => setKeyword(e.target.value) }/>
                </Col>
                <Col
                    xs={24}
                    md={24}
                >
                    <Table
                        columns={subHospitalCol({ handleDelete, limit, page })}
                        dataSource={subHospitalData?.personalInfos}
                        rowKey={record => record.id}
                        pagination={{
                            total: subHospitalData?.paginator?.totalDocs,
                            // showSizeChanger: true,
                            onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                        }}
                        scroll={{ x: 240 }}
                        sticky
                        expandRowByClick={true}
                        // expandedRowKeys
                        expandable={{
                            
                            expandedRowRender: record => (
                            <List
                                // bordered
                                dataSource={record.hospitalizations}
                                renderItem={item => (
                                    <List.Item>
                                        <Row>
                                            <Col
                                                xs={24}
                                                md={24}
                                            >
                                                <Typography.Text type={item.coorporate ? "danger" : "success"}>[{item.coorporate ? "" : ""}]  </Typography.Text><br/>
                                                &emsp;{item.covidVariant}<br/>
                                                &emsp;{moment(item.date_in).format("DD MM YYYY")}<br/>
                                                &emsp;{moment(item.date_out).format("DD MM YYYY")}<br/>
                                                &emsp;{item.description}<br/><br/>
                                                </Col>
                                                <Col
                                                    xs={24}
                                                    md={24}
                                                >
                                                    <br/>
                                                    <Button onClick={()=> {}}><CopyOutlined  style={{ color: "green" }}/></Button>
                                                    <Button onClick={()=> {}}><EditOutlined  style={{ color: "green" }}/></Button>
                                                    <Popconfirm
                                                        title="?"
                                                        onConfirm={ ()=>{handleDelete(item.id, record.id)}}
                                                        okText=""
                                                        cancelText=""
                                                    >
                                                        <Button><DeleteOutlined style={{ color: "red" }} /></Button>
                                                    </Popconfirm>
                                                </Col>
                                        </Row>
                                    </List.Item>
                                    )}
                                />
                            ),
                        }}
                    />
                </Col>
            </Row>
        </>
    )
}
