import React from "react";
import { Card, CardContent, Typography } from "@material-ui/core";
import "./InfoBox.css";
import { AnimationWrapper } from 'react-hover-animation'

function InfoBox({ title, cases, total, active, isRed,isBlue, ImageShow, ...props }) {
  let r = -80;
  if(title===""){
    r = -120;
  }else if(title ===""){
    r = -130;
  }
  return (
    <AnimationWrapper
    // style={{width:"100%"}}
    >
      <Card
        onClick={props.onClick}
        className={`infoBox ${active && "infoBox--selected"} ${isRed && "infoBox--red"} ${isBlue && "infoBox--blue"} `}
        style={{ position: 'relative' }}
      >
        <CardContent>
          {/* <Typography  variant="h5" color="textSecondary" gutterBottom >
            
            {title}
          </Typography> */}
          <h4 className="infoBox_font">{title}</h4>
          <h3 className={`infoBox__cases ${!isRed && "infoBox__cases--green"}`}>
             {cases}
          </h3>
          <Typography as="" className=" infoBox_font" color="textSecondary">
             {total}
          </Typography>
          <img src={ImageShow} style={{
            position: "absolute", top: -30,right:r
          }} />
        </CardContent>
      </Card>
    </AnimationWrapper>
  );
}

export default InfoBox;
