import React, {useState} from 'react'
import {Row, Col, DatePicker, Divider, Button, Typography} from 'antd'
import moment from 'moment'
import ExportToExcel from '../component/export/exportToExcel'
import {useQuery} from '@apollo/client'
import {reportSiemReap} from '../asset/exportExcel/reportSiemReap'
// import {GET_REPORT_DATA_BY_DISTRICT} from '../../graphql/report'
import {
    GET_PEOPLE_CONFIRM,
} from '../graphql/report'

const { RangePicker } = DatePicker
const { Title } = Typography

export default function ReportExcel() {

    const [ranges, setRanges] = useState({
        startDate: new Date(moment().hour(0).minute(0).second(0)),
        endDate: new Date(moment().hour(23).minute(59).second(59)),
    })

    const [reportPeopleConfirm, setReportPeopleConfirm] = useState([])
    const {data: reportPeopleConfirmData} = useQuery(GET_PEOPLE_CONFIRM,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({getPeopeleConfirmRecoverAndDeath})=>{
            setReportPeopleConfirm(getPeopeleConfirmRecoverAndDeath)
        }
    })

    console.log(reportPeopleConfirm)

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = new Date(moment(e[0].hour(0).minute(0).second(0)))
            let endDate = new Date(moment(e[1].hour(23).minute(59).second(59)))

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }
    }

    const dateHolder = ["", ""]

    return (
        <Row>
            <Col
                xs={24}
            >
                <RangePicker placeholder={dateHolder} onChange={e => onSelectDate(e)} />
            </Col>
            <Col
                xs={24}
                md={24}
                lg={12}
            >
                <Row>
                    <Divider><Title level={4}></Title></Divider>
                    <Col
                        xs={12}
                    >
                        <ul>
                            <li><Title level={5}>-</Title></li>
                            <ol>
                                <li>
                                    <ExportToExcel 
                                        headers={reportSiemReap} 
                                        data={
                                            reportPeopleConfirm.confirm
                                        } 
                                        filename={" " + moment(ranges.startDate).format("DD-MM-YYYY") + " " + moment(ranges.endDate).format("DD-MM-YYYY")} 
                                        title={""}
                                        classname={"ant-btn ant-btn-link"}
                                    />
                                </li>
                                <li>
                                    <ExportToExcel 
                                        headers={reportSiemReap} 
                                        data={
                                            reportPeopleConfirm.confirm
                                        } 
                                        filename={" " + moment(ranges.startDate).format("DD-MM-YYYY") + " " + moment(ranges.endDate).format("DD-MM-YYYY")} 
                                        title={""}
                                        classname={"ant-btn ant-btn-link"}
                                    />
                                </li>
                                <li>
                                    <ExportToExcel 
                                        headers={reportSiemReap} 
                                        data={
                                            reportPeopleConfirm.confirm
                                        } 
                                        filename={" " + moment(ranges.startDate).format("DD-MM-YYYY") + " " + moment(ranges.endDate).format("DD-MM-YYYY")} 
                                        title={""}
                                        classname={"ant-btn ant-btn-link"}
                                    />
                                </li>
                            </ol>
                        </ul>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}
