import { Button, Modal, Row, Col, Divider, DatePicker } from 'antd';
import React, { useRef, useState, useEffect } from 'react'
import ReactToPrint from 'react-to-print';
import DatePickerTwo from '../component/datepickertwo/datePickerTwo';
import PrintReport from '../component/print/report';
import PrintReport1 from '../component/print/report1';
import PrintReport2 from '../component/print/report2';
import PrintReport3 from '../component/print/reportPersonalInfo';
import PrintReport4 from '../component/print/report3';
import PrintReport5 from '../component/print/report4';
import PrintReport6 from '../component/print/reportSiemReap';
import moment from 'moment'

import {useQuery} from '@apollo/client'
// import {GET_REPORT_DATA_BY_DISTRICT} from '../../graphql/report'
import {
    GET_PEOPLE_CONFIRM, 
    GET_REPORT_DATA_BY_LOCATION, 
    GET_REPORT_DATA_BY_DISTRICT
} from '../graphql/report'

const { RangePicker } = DatePicker

export default function Report() {
    const componentRef = useRef()
    const componentRef1 = useRef()
    const componentRef2 = useRef()
    const componentRef3 = useRef()
    const componentRef4 = useRef()
    const componentRef5 = useRef()
    const componentRef6 = useRef()
    const [openModal, setOpenModal] = useState(false)
    const [openModal1, setOpenModal1] = useState(false)
    const [openModal2, setOpenModal2] = useState(false)
    const [openModal3, setOpenModal3] = useState(false)
    const [openModal4, setOpenModal4] = useState(false)
    const [openModal5, setOpenModal5] = useState(false)
    const [openModal6, setOpenModal6] = useState(false)
    const [ranges, setRanges] = useState({
        startDate:new Date(),
        endDate: new Date(),
    })

    //report
    const [reportDataByDistrict, setReportDataByDistrict] = useState([])
    const {data:reportData}= useQuery(GET_REPORT_DATA_BY_DISTRICT,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({getDataForReport})=>{
            setReportDataByDistrict(getDataForReport)
        }
    })

    const [reportDataByLocation, setReportDataByLocation] = useState([])
    const {data:reportDataLocation}= useQuery(GET_REPORT_DATA_BY_LOCATION,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({affectedLocationReport})=>{
            setReportDataByLocation(affectedLocationReport)
        }
    })

    const [reportPeopleConfirm, setReportPeopleConfirm] = useState([])
    const {data: reportPeopleConfirmData} = useQuery(GET_PEOPLE_CONFIRM,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({getPeopeleConfirmRecoverAndDeath})=>{
            setReportPeopleConfirm(getPeopeleConfirmRecoverAndDeath)
        }
    })

    useEffect(()=>{
        if(reportData){
            setReportDataByDistrict(reportData?.getDataForReport)
        }
    },[reportData])

    console.log(reportDataByLocation)
    console.log(reportDataByDistrict)

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = new Date(e[0])
            let endDate = new Date(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })

            console.log(ranges)
        }
    }

    const dateHolder = ["", ""]

    return (
        <div>
            <Row>
                <Col
                    xs={24}
                    md={24}
                    lg={24}
                >
                    <RangePicker placeholder={dateHolder} defaultValue={ranges} onChange={e => onSelectDate(e)} />
                </Col>
                <Col
                    xs={24}
                    md={24}
                    lg={12}
                >
                    <Row>
                        <Divider></Divider>
                        <Col
                            xs={{span: 24, offset: 0}}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                 
                            </Button>
                        </Col>
                        <Col
                            xs={{span: 24, offset: 0}}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal1(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Divider></Divider>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal2(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal4(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                                
                            </Button>
                        </Col>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal5(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Divider></Divider>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal6(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                - 
                            </Button>
                        </Col>
                    </Row>
                </Col>
            </Row>



            <Modal
                width={1000}
                visible={openModal}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef.current}
                    />
                ]}
                onCancel={() => setOpenModal(false)}
            >
                <PrintReport ref={componentRef} date={ranges} reportDataByDistrict={reportDataByDistrict} dataLocation={reportDataByLocation} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal1}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef1.current}
                    />
                ]}
                onCancel={() => setOpenModal1(false)}
            >
                <PrintReport1 ref={componentRef1} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal2}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef2.current}
                    />
                ]}
                onCancel={() => setOpenModal2(false)}
            >
                <PrintReport2 ref={componentRef2} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal3}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef3.current}
                    />
                ]}
                onCancel={() => setOpenModal3(false)}
            >
                <PrintReport3 ref={componentRef3} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal4}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef4.current}
                    />
                ]}
                onCancel={() => setOpenModal4(false)}
            >
                <PrintReport4 ref={componentRef4} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal5}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef5.current}
                    />
                ]}
                onCancel={() => setOpenModal5(false)}
            >
                <PrintReport5 ref={componentRef5} />
            </Modal>

            <Modal
                width={900}
                visible={openModal6}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef6.current}
                    />
                ]}
                onCancel={() => setOpenModal6(false)}
            >
                <PrintReport6 ref={componentRef6} data={reportPeopleConfirm} date={ranges} />
            </Modal>
        </div>
    )
}
