import React,{useState} from 'react';
import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const sampleCol = ({handleSampleTestDelete,limit,page}) => {
    // let no = ((page-1) * limit)
    
    let no = (page * limit) - limit
    
    var array = [
        {
            title: '.',
            dataIndex: 'no',
            key: 'no',
            width: 30,
            // render: (text, record) => {
                // if(no/limit === 1){
                //     no = 0
                // }
               
            // return(
            //     <>
            //         {no+=1}
            //     </>
            // )},
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 80,
            render: (text, record) => (
                <span>
                    {record?.lastName+" "+record?.firstName}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'confirmedAt',
            key: 'confirmedAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.confirmedAt === null ? "" : moment.parseZone(record?.currentState?.confirmedAt).format("DD MM YYYY")}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'deathAt',
            key: 'deathAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.deathAt === null ? "" : moment.parseZone(record?.currentState?.deathAt).format("DD MM YYYY")}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'times',
            key: 'times',
            width: 60,
            render: (text, record) => (
                <span>
                    {record?.sampleTest?.length} 
                </span>
            ),
        },
        // {
        //     title: '',
        //     dataIndex: 'location',
        //     key: 'location',
        //     width: 50,
        // },
        {
            title: '',
            dataIndex: 'result',
            key: 'result',
            width: 60,
            render: (text, record) => {
                let state ="";
                let classN = "badge-success"
                let c_state = record?.currentState;

                if(c_state.confirm===true){
                    state = ""
                    classN= "badge-error"
                }

                if(c_state.recovered===true){
                    state = ""
                    classN= "badge-success"
                }

                if(c_state.death===true){
                    state = ""
                    classN= "badge-error"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
        },
        // {
        //     title: '',
        //     dataIndex: 'symptom',
        //     key: 'symptom',
        //     width: 50,
        // },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 100,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    {/* <Popconfirm
                        title="?"
                        onConfirm={() => { handleSampleTestDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]

    return array
}