import React, { useEffect, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, DatePicker,message,Table,Divider  } from 'antd'

import { useMutation,useQuery } from '@apollo/client';
import { ADD_VACCINATION,DELETE_VACCINATION } from '../../../graphql/people';
import moment from 'moment'
import { vaccinationCol } from '../tableColumn/vaccinationColumn';

const {Option} = Select

export default function EditVaccination({ open, setOpen, peopleID, setRefetch,dataVaccination }) {

    let [form] = Form.useForm()
    const [vaccineData,setVaccineData] = useState([])

    useEffect(()=>{
        if(dataVaccination!==undefined){
            setVaccineData(dataVaccination)
        }
    },[dataVaccination])

    const [addVaccination,{loading}] = useMutation(ADD_VACCINATION,{
        onCompleted:({addVaccination})=>{
            if(addVaccination.success===true){
                setRefetch()
                message.success("")
            }else{
               
                message.error(addVaccination.message)
            }
            
        }
    })

    const [deleteVaccination,{loading:deleteLoading, error:deleteEorror}] = useMutation(DELETE_VACCINATION,{
        onCompleted:({deleteVaccination})=>{
            if(deleteVaccination.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(deleteVaccination.message)
            }
            
        },
        onError:(errors)=>{
            message.error(errors.message)
        }
    })

    const onFinish = (values) => {

        addVaccination({
            variables:{
                ...values,
                date:moment(values.date).format(),
                times: parseInt(values.times),
                personalInfoId:peopleID
            }
        })

        // setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleDelete=(e)=>{
        console.log(e)
        deleteVaccination({
            variables:{
                personalInfoId:peopleID,
                vaccinationId:e
            }
        })
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="editVaccination"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                 <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                    <Divider />
                    <Col xs={24}>
                        <Table
                            columns={vaccinationCol({handleDelete})}
                            dataSource={vaccineData}
                            pagination={false}
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
