import React, {useState} from 'react'
import { Modal, Row, Col, Timeline, Button } from 'antd'
import Tab1 from '../newForm/tab1'
import Tab2 from '../newForm/tab2'
import Tab3 from '../newForm/tab3'

export default function NewForm({open, setOpen}) {
    const [openTab1, setOpenTab1] = useState(false)
    const [openTab2, setOpenTab2] = useState(false)
    const [openTab3, setOpenTab3] = useState(false)
    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title=""
        >
            <Tab1 open={openTab1} setOpen={setOpenTab1} />
            <Tab2 open={openTab2} setOpen={setOpenTab2} />
            <Tab3 open={openTab3} setOpen={setOpenTab3} />

            <Row>
                <Col
                    xs={24}
                >
                    <Timeline>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab1(true)}
                            >
                                intro
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab2(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab3(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                            >
                                .
                            </span>
                        </Timeline.Item>
                    </Timeline>,
                </Col>
            </Row>
        </Modal>
    )
}
