import React from 'react'
import { Select, Divider, Input } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
const { Option } = Select;

export function ListSelect({ type, data, title, value, setValue, id, disabled }) {
    return (
        type === 0 ? (
            <Select
                showSearch
                style={{ width: "100%" }}
                placeholder={title}
                value={value}
                onChange={e => setValue(e)}
                disabled={disabled}
                optionFilterProp="children"
                filterOption={(input, option) =>
                    option.children.indexOf(input) >= 0
                }
                filterSort={(optionA, optionB) =>
                    optionA.children.localeCompare(optionB.children)
                }
            >
                {data.map(load => {
                    return <Option value={load.title} key={load.id}>{load.title}</Option>
                })}
            </Select>
        ) : (

            type === 1 ? (
                <Select
                    showSearch
                    style={{ width: "100%" }}
                    placeholder={title}
                    value={value}
                    onChange={e => setValue(e)}
                    disabled={disabled}
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.children.indexOf(input) >= 0
                    }
                    filterSort={(optionA, optionB) =>
                        optionA.children.localeCompare(optionB.children)
                    }
                >
                    {data.map(load => {
                        return <Option value={load.title} key={load.title}>{load.title}</Option>
                    })}
                </Select>
            ) : (

                type === 2 ? (
                    <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={title}
                        value={value}
                        onChange={e => setValue(e)}
                        disabled={disabled}
                        optionFilterProp="children"
                        filterOption={(input, option) =>
                            option.children.indexOf(input) >= 0
                        }
                        filterSort={(optionA, optionB) =>
                            optionA.children.localeCompare(optionB.children)
                        }
                    >
                        {data.map(load => {
                            return <Option value={load.id} key={load.id}>{load.lastName} {load.firstName}</Option>
                        })}
                    </Select>
                ) : (

                    type === 3 ? (
                        <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={title}
                            value={value}
                            onChange={e => setValue(e)}
                            disabled={disabled}
                            optionFilterProp="children"
                            filterOption={(input, option) =>
                                option.children.indexOf(input) >= 0
                            }
                            filterSort={(optionA, optionB) =>
                                optionA.children.localeCompare(optionB.children)
                            }
                        >
                            {data.map(load => {
                                return <Option value={load.id} key={load.id}>{load.caseName}</Option>
                            })}
                        </Select>
                    ) :
                        type === 4 ? (
                            <Select
                                showSearch
                                style={{ width: "100%" }}
                                placeholder={title}
                                value={value}
                                onChange={e => setValue(e)}
                                disabled={disabled}
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.children.indexOf(input) >= 0
                                }
                                filterSort={(optionA, optionB) =>
                                    optionA.children.localeCompare(optionB.children)
                                }
                            >
                                <Option value="new" key="new"></Option>
                                {data.map(load => {
                                    return <Option value={load.id} key={load.id}>{load.caseName}</Option>
                                })}
                            </Select>
                        ) :
                            type === 5 ? (
                                <Select
                                    showSearch
                                    style={{ width: "100%" }}
                                    placeholder={title}
                                    value={value}
                                    onChange={e => setValue(e)}
                                    disabled={disabled}
                                    optionFilterProp="children"
                                    filterOption={(input, option) =>
                                        option.children.indexOf(input) >= 0
                                    }
                                    filterSort={(optionA, optionB) =>
                                        optionA.children.localeCompare(optionB.children)
                                    }
                                >
                                    <Option value="new" key="new"></Option>
                                    {data.map(load => {
                                        return <Option value={load.id} key={load.id}>{load.locationName}</Option>
                                    })}
                                </Select>
                            ) : (
                                type === 6 ? (
                                    <Select
                                    showSearch
                                    style={{ width: "100%" }}
                                    placeholder={title}
                                    value={value}
                                    onChange={e => setValue(e)}
                                    disabled={disabled}
                                    optionFilterProp="children"
                                    filterOption={(input, option) =>
                                        option.children.indexOf(input) >= 0
                                    }
                                    filterSort={(optionA, optionB) =>
                                        optionA.children.localeCompare(optionB.children)
                                    }
                                >
                                    <Option value="new" key="new"></Option>
                                    {data.map(load => {
                                        return <Option value={load.id} key={load.id}>{load.title}</Option>
                                    })}
                                </Select>
                                ) : 
                                    type === 7 ?(
                                        <Select
                                            showSearch
                                            style={{ width: "100%" }}
                                            placeholder={title}
                                            value={value}
                                            onChange={e => setValue(e)}
                                            disabled={disabled}
                                            optionFilterProp="children"
                                            filterOption={(input, option) =>
                                                option.children.indexOf(input) >= 0
                                            }
                                            filterSort={(optionA, optionB) =>
                                                optionA.children.localeCompare(optionB.children)
                                            }
                                        >
                                            {data?.map(load => {
                                                return <Option value={load._id===null || load._id=== undefined ? "" : load._id} key={load._id}>{load._id === "" || load._id===null ? "" : load._id}</Option>
                                            })}
                                        </Select>
                                    )
                                :(
                                    <Select
                                        showSearch
                                        style={{ width: "100%" }}
                                        placeholder={title}
                                        value={value}
                                        onChange={e => setValue(e)}
                                        disabled={disabled}
                                        optionFilterProp="children"
                                        filterOption={(input, option) =>
                                            option.children.indexOf(input) >= 0
                                        }
                                        filterSort={(optionA, optionB) =>
                                            optionA.children.localeCompare(optionB.children)
                                        }
                                    >
                                        <Option value={""} key={"None"}></Option>
                                        {data.map(load => {
                                            return <Option value={load.id} key={load.id}>{load.title}</Option>
                                        })}
                                    </Select>
                                )
                            )
                )
            )

        )

    )
}

export function ListUserSelect({ type, data, title, value, setValue, id, disabled }) {
    return (
        type === 0 ? (
            <Select
                showSearch
                style={{ width: "100%" }}
                placeholder={title}
                value={value}
                disabled={disabled}
                optionFilterProp="children"
                onChange={(e) => setValue(e)}
                filterOption={(input, option) =>
                    option.children.indexOf(input) >= 0
                }
                filterSort={(optionA, optionB) =>
                    optionA.children.localeCompare(optionB.children)
                }
            >
                <Option key={0} value="New"></Option>
                {data.map(load => {
                    return (
                        <Option value={load.id} key={load.id}>{load.idCard + " " + load.firstName + " " + load.lastName + " (" + load.gender + ") " + load.tel}</Option>
                    )
                })}
            </Select>
        ) : (
            <Select
                showSearch
                style={{ width: "100%" }}
                placeholder={title}
                value={value}
                disabled={disabled}
                optionFilterProp="children"
                onChange={(e) => setValue(e)}
                filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                filterSort={(optionA, optionB) =>
                    optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())
                }
            >
                <Option key={0} value="New">New</Option>
                {data.map(load => {
                    return (
                        <Option value={load.id} key={load.id}>{load.id + " " + load.name + " (" + load.gender + ")"}</Option>
                    )
                })}
            </Select>
        )
    )
}

export function SelectTestLocation({type, data, title,addItem, value,newValue ,setNewValue,setValue, id, disabled }){
    return (
    <Select
        showSearch
        style={{ width: "100%" }}
        placeholder={title}
        value={value}
        onChange={(e)=>setValue(e)}
        filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
        filterSort={(optionA, optionB) =>
            optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())
        }
        dropdownRender={menu => (
          <div>
            {menu}
            <Divider style={{ margin: '4px 0' }} />
            <div style={{ display: 'flex', flexWrap: 'nowrap', padding: 8 }}>
              <Input style={{ flex: 'auto' }} value={newValue} onChange={(e)=>setNewValue(e.target.value)} />
              <a
                style={{ flex: 'none', padding: '8px', display: 'block', cursor: 'pointer' }}
                onClick={addItem}
              >
                <PlusOutlined /> 
              </a>
            </div>
          </div>
        )}
      >
        {data?.map(d => (
          <Option value={d} key={d}>{d}</Option>
        ))}
       
    </Select>
    )
}

export function SelectPeople({ data, title, value,newValue ,handleSearchPeople,setValue, id, disabled }){
    console.log(data)
    const options = data.map(d => <Option value={d.id} key={d.id}>{d.title}</Option>);
    return(
        <Select
            showSearch
            value={value}
            placeholder={title}
            style={{width:'100%'}}
            defaultActiveFirstOption={false}
            showArrow={false}
            filterOption={false}
            onSearch={(e)=> handleSearchPeople(e)}
            onChange={(e)=>setValue(e)}
            notFoundContent={null}
            disabled={disabled}
        >
            <Option key={0} value="new"></Option>
            {options}
        </Select>
        
    )
}